@import url(https://fonts.googleapis.com/css2?family=Wire+One&display=swap);
html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.app {
    position: relative;
    text-align: center;
    width: 100%;
    height: 100%;
    min-width: 100vw;
    min-height: 100vh;
}

.section {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}

.wrapper-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    width: 90%;
    height: 90%;
    border: 1px solid #ffffff;
}

h1, h2 {
    color: #ffffff;
    padding: 12px 20px;
    /*background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);*/
    /*-webkit-background-clip: text;*/
    /*-webkit-text-fill-color: transparent;*/
    /*background-size: 800% 800%;*/
    /*animation: rainbow 16s ease infinite;*/
}

h1 {
    margin: 0;
    font: 500 111px/111px 'Wire One', sans-serif;
    text-transform: uppercase;
}

h2 {
    margin: 0;
    font: 500 52px/54px 'Wire One', sans-serif;
    text-transform: uppercase;
}

.links {
    display: flex;
    justify-content: center;
    align-items: center;
}

.link {
    width: 42px;
    height: 42px;
    margin-top: 20px;
    margin-left: 40px;
}

.link:first-child {
    margin-left: 0;
}

a {
    display: block;
    cursor: pointer;
    width: 100%;
    height: 100%;
    transition: background-color 100ms ease-in-out;
    text-indent: -9999px;
}

a.github {
    background-color: #ffffff;
    -webkit-mask-image: url(/static/media/github.ada9d110.svg);
            mask-image: url(/static/media/github.ada9d110.svg);
    mask-size: cover;
    -webkit-mask-size: cover;
    -webkit-animation: logo-spin infinite 60s linear;
            animation: logo-spin infinite 60s linear;
}

a.telega {
    background-color: #ffffff;
    -webkit-mask-image: url(/static/media/telega.b22b982a.svg);
            mask-image: url(/static/media/telega.b22b982a.svg);
    mask-size: cover;
    -webkit-mask-size: cover;
    -webkit-animation: logo-spin infinite 10s linear;
            animation: logo-spin infinite 10s linear;
}

a:hover {
    background-color: rgba(245, 245, 245, 0.75);
}

@-webkit-keyframes logo-spin {
    0% {
        transform: rotate(0deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

@keyframes logo-spin {
    0% {
        transform: rotate(0deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

@-webkit-keyframes rainbow {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 25%
    }
    100% {
        background-position: 0 50%
    }
}

@keyframes rainbow {
    0% {
        background-position: 0 50%
    }
    50% {
        background-position: 100% 25%
    }
    100% {
        background-position: 0 50%
    }
}

